@import '../../sass/main.scss';

.header {
  @include flex(row, space-between, center);
  height: 68px;
  max-width: 95%;
  margin: 0 auto;
  @media screen and (max-width: 900px) {
    height: 40px;
  }
  img {
    height: 60%;
    width: 40%;
    @media screen and (max-width: 700px) {
      height: 40px;
    }
  }
}
